.btn {
  display: inline-block;
  padding: 6px 10px;
  line-height: 1.42857143;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: normal;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  user-select: none;
  background-image: none;
  border: 1px solid transparent;
  border-radius: 4px;
  box-sizing: border-box;
}
.btn-gray {
  height: 32px;
  color: #333;
  background-color: #F0F0F0;
}
.btn-gray:hover,
.btn-gray.hover,
.btn-gray:focus,
.btn-gray.focus,
.btn-gray:active,
.btn-gray.active {
  background-color: #E6E6E6;
}



